import React from 'react'
import Link from 'gatsby-link'
import Counter from '../components/counter'

const SecondPage = () => (
  <div>
    <h1>Products</h1>
    <div>
      <Counter></Counter>
    </div>
    <Link to="/">Go back to the homepage</Link>
  </div>
)

export default SecondPage


