import React from "react";

class Counter extends React.Component {

  constructor() {
    super()
    this.state = { count: 0 }
  }

  render() {
    return (
      <div style={{background: '#888', width: '10rem', padding: '0rem'}}>
        <h4 style={{background: '#333', color: '#fff', lineHeight: '1.5rem', paddingLeft: '.3rem', width: '10rem', margin: '0'}}>Counter</h4>
        <p style={{paddingLeft: '.3rem'}}>current count: {this.state.count}</p>
        <button onClick={() => this.setState({ count: this.state.count +  1 })} >plus </button>
        <button onClick={() => this.setState({ count: this.state.count - 1 })} >minus </button>
      </div>
    )
  }
}

export default Counter;


